@font-face {
  font-family: "GT Walsheim Pro";
  src: url("../fonts/GTWalsheimPro/GTWalsheimPro-Bold.eot");
  src: local("GT Walsheim Pro Bold"), local("GTWalsheimPro-Bold"),
    url("../fonts/GTWalsheimPro/GTWalsheimPro-Bold.eot") format("eot"),
    url("../fonts/GTWalsheimPro/GTWalsheimPro-Bold.woff2") format("woff2"),
    url("../fonts/GTWalsheimPro/GTWalsheimPro-Bold.woff") format("woff"),
    url("../fonts/GTWalsheimPro/GTWalsheimPro-Bold.ttf") format("ttf");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "GT Walsheim Pro";
  src: url("../fonts/GTWalsheimPro/GTWalsheimPro-Regular.eot");
  src: local("GT Walsheim Pro Regular"), local("GTWalsheimPro-Regular"),
    url("../fonts/GTWalsheimPro/GTWalsheimPro-Regular.eot") format("eot"),
    url("../fonts/GTWalsheimPro/GTWalsheimPro-Regular.woff2") format("woff2"),
    url("../fonts/GTWalsheimPro/GTWalsheimPro-Regular.woff") format("woff"),
    url("../fonts/GTWalsheimPro/GTWalsheimPro-Regular.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
}
* {
  margin: 0;
  padding: 0;
}
$yellow: #f9dd64;
$grey: #dedfe1;
@mixin gridcenter() {
  display: grid;
  align-items: center;
  justify-items: center;
  justify-content: center;
}
@mixin flexrow() {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@mixin flexcol() {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@mixin borderpage() {
  border: solid #000;
  border-width: 1px 0 1px;
}
@mixin font700() {
  font-family: "GT Walsheim Pro";
  font-style: normal;
  font-weight: 700;
  font-size: 44px;
  line-height: 92.5%;
}
@mixin font400() {
  font-family: "GT Walsheim Pro";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
}
@mixin ladderp() {
  position: relative;
  @include font700();
  height: 55px;
  display: flex;
  text-transform: uppercase;
  align-items: center;
  border: 1px solid #000;
  padding-left: 5px;
}
@mixin pborder() {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  border: 1px solid #000;
  width: 100%;
  height: 100%;
  z-index: -1;
}
body {
  header {
    height: 100px;
    @include gridcenter();
    grid-template-columns: 2fr 6fr 2fr;
    nav {
      @include flexrow();
      gap: 5px;
      width: 50%;
      text-transform: capitalize;
      a {
        text-decoration: none;
        color: #000;
        @include font400();
      }
    }
    .login {
      justify-items: end;
      width: 80%;
      @include flexrow();
      button {
        @include font400();
        text-transform: capitalize;
        width: 90px;
        height: 50px;
        background: transparent;
        border: 0;
      }
      .submit {
        background: $yellow;
        border: 1px solid #000;
        position: relative;
      }
      .submit:after {
        content: "";
        position: absolute;
        z-index: -1;
        top: 2px;
        left: -5px;
        width: 100%;
        height: 50px;
        border: 1px solid #000;
      }
    }
  }
  header::after {
    content: "";
    position: absolute;
    top: 100px;
    left: 3%;
    width: 95%;
    height: 10px;
    border: 1px solid black;
    border-top: 0;
  }
  .start_page {
    width: 100%;
    height: 840px;
    @include flexrow();
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #000;
    .inf {
      width: 40%;
      height: 400px;
      @include flexcol();
      img {
        width: 100%;
      }
      p {
        @include font400();
        font-size: 20px;
        line-height: 120%;
      }
      .buttons {
        width: 60%;
        @include flexrow();
        gap: 10px;
        button {
          @include font400();
          height: 50px;
          position: relative;
        }
        button:after {
          content: "";
          position: absolute;
          z-index: -1;
          top: 2px;
          left: 3px;
          width: 100%;
          height: 50px;
          border: 1px solid #000;
        }
        .try {
          width: 120px;
          background: #fff;
          border: 1px solid #000;
        }
        .submit {
          background: $yellow;
          width: 215px;
          border: 1px solid #000;
        }
      }
    }
    .img {
      width: 52%;
      img {
        width: 100%;
      }
    }
  }
  .mean {
    margin-top: 20px;
    @include borderpage();
    width: 100%;
    height: 800px;
    @include flexcol();
    align-items: center;
    justify-content: center;
    .inf {
      width: 43%;
      height: 130px;
      @include flexcol();
      p {
        margin-top: 25px;
        width: 85%;
        @include font400();
        font-size: 20px;
        text-align: center;
        align-self: center;
        height: 70px;
      }
    }
    .pictures {
      width: 90%;
      @include flexrow();
      align-items: flex-end;
      .ladder {
        @include flexcol();
        width: 40%;
        height: 300px;
        p {
          @include ladderp();
        }
        p:after {
          @include pborder();
        }
        p:nth-child(odd) {
          background: #000;
          color: #fff;
        }
        p:nth-child(1) {
          width: 55%;
        }
        p:nth-child(2) {
          background: $grey;
          width: 65%;
        }
        p:nth-child(3) {
          width: 77%;
        }
        p:nth-child(4) {
          background: #fff;
          width: 86%;
        }
        p:nth-child(5) {
          width: 100%;
        }
      }
      .img {
        width: 40%;
        img {
          width: 100%;
        }
      }
    }
  }
  .rotate {
    @include font700();
    font-size: 20px;
    transform: rotate(270deg) translate(-20px, 40px);
  }
  .get {
    margin-top: 20px;
    width: 100%;
    height: 1025px;
    border: solid #000;
    border-width: 1px 0 1px;
    position: relative;
    display: grid;
    .img {
      width: 30%;
      grid-column: 3/13;
      grid-row: 3/4;
      align-self: center;
    }
    .rotate {
      grid-column: 2/3;
      grid-row: 3/4;
      justify-items: center;
    }
    .blocks {
      grid-column: 3/13;
      grid-row: 4/13;
      width: 100%;
      @include flexrow();
      align-items: baseline;
      flex-wrap: wrap;
      .block {
        width: 32%;
        height: 45%;

        h1 {
          margin: 18px 0 10px;
          @include font700();
          font-size: 38px;
        }
        p {
          @include font400();
          font-size: 20px;
        }
      }
    }
  }
  .skills {
    margin: 100px 5%;
    width: 95%;
    height: 405px;
    img {
      width: 40%;
    }
    p {
      width: 45%;
      @include font400();
      font-size: 20px;
      margin: 25px 0 65px;
    }
    .wrap {
      height: 220px;
      @include flexrow();
      align-items: center;
      .ladder {
        align-self: flex-end;
        @include flexcol();
        height: 85%;
        width: 30%;
        p {
          color: #fff;
          margin: 0;
          width: 70%;
          @include ladderp();
        }
        p:nth-child(odd) {
          background: #000;
        }
        p:nth-child(2) {
          background: $grey;
          width: 82%;
        }
        p:nth-child(3) {
          width: 100%;
        }
        p:after {
          @include pborder();
        }
      }
      .img {
        width: 40%;

        display: flex;
        justify-content: center;
        img {
          width: 70%;
        }
      }
      .result {
        width: 32%;
        height: 100%;
        p {
          color: #fff;
          @include font400();
          font-size: 44px;
          margin: 0;
          width: 100%;
          -webkit-text-stroke: 1px #000;
          text-shadow: 3px 2px 0 #000;
        }
      }
    }
  }
}
